
:root {
  font-size: 18px;
  font-family: "Karla", sans-serif;
  font-weight: 400;
  --transition-speed: 600ms;
}

.dark {
  --text-primary: #f0e6ef;
  --text-secondary: #ffffff;
  --bg-primary: #2c2b3c;
  --bg-clear: #403f4c;
  --bg-secondary: #121420;
  --pink-color: #f0a6ca;
  --auxiliar-color: #b8bedd;
}

.light {
  --text-primary: #1f1f1f;
  --text-secondary: #000000;
  --bg-primary: #f5d8ef;
  --bg-clear: #f0a6ca;
  --bg-secondary: #f0e6ef;
  --pink-color: #9c89b8;
  --auxiliar-color: #b8bedd;
}

.theme-icon {
  display: none;
}

.dark #darkIcon {
  display: block;
}

.light #lightIcon {
  display: block;
}

strong {
  color: var(--pink-color);
}

.Home {
  margin: 0;
  padding: 0;
}

body {
  color: black;
  background-color: white;
  overflow-y: scroll;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 0.25rem;
}

body::-webkit-scrollbar-track {
  background-color: black;
}

body::-webkit-scrollbar-thumb {
  background-color: #ffcad4;
}

.App {
